import React from 'react';
import { StyleSheet, View } from 'react-native';
import { useDimensions } from 'src/utils';
import { CommonProps } from 'src/types';
import { Tab } from './Components';

export function Tabs({
  style,
  labels,
  selectedIndex,
  onChangeTab,
}: TabsProps) {
  const tabRefs = labels.map(() => useDimensions());
  const selectedTabDimensions = tabRefs[selectedIndex] && tabRefs[selectedIndex][1];

  return (
    <View style={[styles.tabs, style]}>
      <View style={styles.tabsListWrapper}>
        {
          labels.map((label, index) => (
            <Tab
              label={label}
              isSelected={index === selectedIndex}
              onPress={() => index !== selectedIndex && onChangeTab(index)}
              key={`label-${index}`}
              ref={tabRefs[index][0]}
            />
          ))
        }
      </View>
      <View
        // @ts-ignore
        style={[styles.tabsSelectHighlight, {
          transform: [
            `translate(${selectedTabDimensions.x || 0}px, 0)`,
            `scale(${(selectedTabDimensions.width || 0) / 10},`,
            `${(selectedTabDimensions.height || 0) / 10})`,
          ].join(''),
        }]}
      />
    </View>
  );
}

interface TabsProps extends CommonProps {
  labels: string[],
  selectedIndex: number,
  onChangeTab: (tabIndex: number) => void,
}

const styles = StyleSheet.create({
  tabs: {
    // @ts-ignore
    position: 'static',
    marginTop: 10,
    marginLeft: 15,
  },
  tabsListWrapper: {
    display: 'flex',
    flexDirection: 'row',
    zIndex: 1,
  },
  tabsSelectHighlight: {
    position: 'absolute',
    left: -10,
    bottom: 0,
    width: 10,
    height: 10,
    backgroundColor: 'yellowgreen',
    // @ts-ignore
    transformOrigin: 'bottom left',
    transition: 'transform 0.5s ease',
  },
});
