import React, { useState } from 'react';
import { StyleSheet, Text, TextStyle } from 'react-native';
import { CommonProps } from 'src/types';

export function TextButton({
  style,
  hoverStyle,
  isDisabled,
  onPress,
  children,
}: TextButtonProps) {
  const [hasHover, setHasHover] = useState(false);

  return (
    <Text
      style={[
        styles.textButton,
        hasHover && styles.buttonHasHover,
        style,
        hasHover && hoverStyle,
        isDisabled && styles.buttonIsDisabled,
      ]}
      // @ts-ignore
      onMouseEnter={() => setHasHover(true)}
      onMouseLeave={() => setHasHover(false)}
      onPress={(event) => !isDisabled && onPress && onPress(event)}
    >
      {children}
    </Text>
  );
}

interface TextButtonProps extends CommonProps {
  hoverStyle?: TextStyle,
  isDisabled?: boolean,
}

const styles = StyleSheet.create({
  textButton: {
    padding: 5,
    textAlign: 'center',
    justifyContent: 'center',
    // @ts-ignore
    borderWidth: '1px',
    borderColor: '#ccc',
    cursor: 'pointer',
    transition: 'opacity 0.5s ease, background-color 0.5s ease, border-color 0.5s ease',
  },
  buttonHasHover: {
    // @ts-ignore
    opacity: '0.8',
  },
  buttonIsDisabled: {
    // @ts-ignore
    opacity: '0.5',
  },
});

TextButton.styles = styles;
