import { useState, useLayoutEffect, useRef } from 'react';

function getDimensionObject(node: HTMLElement): DimensionObject {
  const rect = node.getBoundingClientRect();

  return {
    width: rect.width,
    height: rect.height,
    top: typeof rect.x === 'number' ? rect.x : rect.top,
    left: typeof rect.y === 'number' ? rect.y : rect.left,
    x: typeof rect.x === 'number' ? rect.x : rect.left,
    y: typeof rect.y === 'number' ? rect.y : rect.top,
    right: rect.right,
    bottom: rect.bottom,
  };
}

export function useDimensions({
  liveMeasure = true,
}: UseDimensionsArgs = {}): UseDimensionsHook {
  const [dimensions, setDimensions] = useState({} as DimensionObject);

  const nodeRef = useRef<HTMLElement>(null);

  useLayoutEffect(() => {
    const node = nodeRef.current;

    if (node) {
      const measure = () => window.requestAnimationFrame(
        () => setDimensions(getDimensionObject(node)),
      );
      measure();

      if (liveMeasure) {
        window.addEventListener('resize', measure);
        window.addEventListener('scroll', measure);

        return () => {
          window.removeEventListener('resize', measure);
          window.removeEventListener('scroll', measure);
        };
      }
    }

    return () => {};
  }, []); // [nodeRef]

  return [nodeRef, dimensions];
}

type DimensionObject = {
  width: number;
  height: number;
  top: number;
  left: number;
  x: number;
  y: number;
  right: number;
  bottom: number;
};

type UseDimensionsArgs = {
  liveMeasure?: boolean;
};

type UseDimensionsHook = [
  React.MutableRefObject<HTMLElement | null>,
  DimensionObject,
];
