import React, { useState } from 'react';
import { StyleSheet, Text } from 'react-native';
import { CommonProps } from 'src/types';

export const Tab = React.forwardRef<HTMLElement, TabProps>(({
  style,
  label,
  isSelected,
  onPress,
}, ref) => {
  const [hasHover, setHasHover] = useState(false);

  return (
    <Text
      style={[
        styles.tab,
        style,
        hasHover && styles.tabHasHover,
        isSelected && styles.tabIsSelected,
      ]}
      // @ts-ignore
      ref={ref}
      // @ts-ignore
      onMouseEnter={() => setHasHover(true)}
      onMouseLeave={() => setHasHover(false)}
      onPress={onPress}
    >
      {label}
    </Text>
  );
});

interface TabProps extends CommonProps {
  label: string,
  isSelected: boolean,
}

const styles = StyleSheet.create({
  tab: {
    paddingTop: 20,
    paddingBottom: 20,
    paddingLeft: 15,
    paddingRight: 15,
    fontSize: 16,
    fontWeight: 'bold',
    color: '#666',
    cursor: 'pointer',
    transition: 'background-color 0.5s ease',
  },
  tabHasHover: {
    backgroundColor: '#f0f0f0',
  },
  tabIsSelected: {
    backgroundColor: 'none',
    transition: 'none',
    cursor: 'none',
  },
});
