import React from 'react';
import { StyleSheet, Text as RNText } from 'react-native';
import { CommonProps } from 'src/types';
import { RowWrap } from './RowWrap';
import { TextButton } from './TextButton';

export function SelectButtons({
  style,
  labelText,
  isRequired,
  isHidden,
  options,
  extraText,
  selectedIndex,
  onChange,
}: SelectButtonsProps) {
  return (
    <RowWrap
      style={style}
      labelText={labelText}
      isRequired={isRequired}
      isHidden={isHidden}
    >
      {
        options.map((optionText, index) => (
          <TextButton
            style={[
              styles.button,
              index === selectedIndex && styles.buttonIsSelected,
            ]}
            onPress={() => index !== selectedIndex && onChange(index)}
            key={`button-${index}`}
          >
            {optionText}
          </TextButton>
        ))
      }

      <RNText style={styles.extraText}>{extraText}</RNText>
    </RowWrap>
  );
}

interface SelectButtonsProps extends CommonProps {
  labelText: string,
  isRequired?: boolean,
  isHidden?: boolean,
  options: string[],
  selectedIndex: number,
  extraText?: string,
  onChange: (index: number) => void,
}

const styles = StyleSheet.create({
  button: {
    padding: 10,
    paddingTop: 6,
    paddingBottom: 6,
    borderWidth: 1,
    borderColor: '#eee',
  },
  buttonIsSelected: {
    borderColor: 'yellowgreen',
    backgroundColor: 'yellowgreen',
    // @ts-ignore
    opacity: '1',
  },
  extraText: {
    paddingTop: 6,
    paddingLeft: 12,
    fontSize: 16,
    color: '#ccc',
  },
});
