import React from 'react';
import { StyleSheet, SectionList, Text } from 'react-native';

export function Instructions() {
  const sections: DataSection[] = [
    {
      title: 'Expenses categories',
      data: [
        [
          '// common',
          ['meal', 'trans', 'accountant', 'mobile'].join(', '),
          '// other',
          ['web', 'software', 'hardware', 'stuff', 'ent', 'home', 'legal', 'office', 'rent', 'subscriptions', 'consultant'].join(', '),
        ],
      ],
    },
    {
      title: 'Sync step',
      data: [
        [
          '1. Sync should work automatically if monzo credential is valid',
          '2. update descriptions of synced transactions',
          '3. check "confirm" for next sync, or do "just confirm"',
        ],
      ],
    },
    {
      title: 'Expenses step',
      data: [
        [
          '1. Set Expenses creates new spreadsheet if not exists',
          '2. add category, and freeagent expense label in spreadsheet',
          '3. changed values & dates on any row only affects row of added expense (use this to expense e.g. weekend items)',
        ],
      ],
    },
  ];

  return (
    <>
      <SectionList<DataItem, DataSection>
        sections={sections}
        renderSectionHeader={renderHeader}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${item}-${index}`}
      />
    </>
  );
}

function renderItem({ item }: { item: DataItem }) {
  return (
    <>
      {item.map((row, index) => (
        <Text
          style={styles.item}
          key={`row-${index}`}
        >
          {row}
        </Text>
      ))}
    </>
  );
}

function renderHeader({ section: { title } }: { section: DataSection }) {
  return (
    <Text style={styles.header}>{title}</Text>
  );
}

type DataItem = string[];

type DataSection = {
  title: string,
  data: DataItem[],
};

const styles = StyleSheet.create({
  header: {
    padding: 5,
    paddingTop: 15,
    fontSize: 18,
  },
  item: {
    paddingLeft: 10,
  },
});
