import React from 'react';
import {
  StyleSheet,
  Text,
  View,
} from 'react-native';
import { CommonProps } from 'src/types';

export function RowWrap({
  style,
  labelText,
  isRequired,
  isHidden,
  children,
}: RowWrapProps) {
  return (
    <View
      style={[
        styles.row,
        style,
        isHidden ? styles.rowIsHidden : {},
      ]}
    >
      <Text style={styles.label}>
        {labelText}
        {labelText && isRequired && (
          <Text style={styles.labelIsRequired}>*</Text>
        )}
        {labelText && ':'}
      </Text>

      {children}
    </View>
  );
}

interface RowWrapProps extends CommonProps {
  labelText: string,
  isRequired?: boolean,
  isHidden?: boolean,
}

const styles = StyleSheet.create({
  row: {
    paddingTop: 20,
    paddingLeft: 15,
    backgroundColor: 'white',
    fontSize: 16,
    display: 'flex',
    flexDirection: 'row',
    // @ts-ignore
    transition: 'transform 0.5s ease',
  },
  rowIsHidden: {
    paddingTop: 0,
    height: 0,
    // @ts-ignore
    transform: 'translate(0, -100%)',
  },
  label: {
    paddingTop: 6,
    paddingRight: 12,
    minWidth: 150,
    fontSize: 16,
  },
  labelIsRequired: {
    paddingLeft: 2,
    paddingRight: 2,
    color: 'tomato',
  },
});
