import React, { useState } from 'react';
import { ENDPOINTS, USER_ID, isNumeric } from 'src/globs';
import { Inputs, Main } from 'src/Components';
import {
  OnFetchFn, SyncMonzoHandlerBodyParams,
  TransactionFormatOption, TransactionResolveOption,
} from 'src/types';

export function SyncTransactions({
  spreadsheetId,
  cachedFields,
  isDisabled,
  onFetchAction,
}: SyncTransactionsProps) {
  // todo temp page (maybe move to redux?)
  const [sheetId, setSheetId] = useState(cachedFields?.sheetId);
  const [sheetIdExtra, setSheetIdExtra] = useState(cachedFields?.sheetIdExtra);
  const [activeCurrency, setActiveCurrency] = useState(cachedFields?.activeCurrency);
  const [dateRange, setDateRange] = useState(['', ''] as [string, string]);
  const [formatIndex, setFormatIndex] = useState(
    getFormatIndex(cachedFields?.formatOption),
  );
  const [timeDiff, setTimeDiff] = useState(cachedFields?.timeDiff);
  const [resolveOptionSelections, setResolveOptionSelections] = useState([false, false]);

  const isSheetIdExtraRequired = checkIsSheetIdExtraRequired(FORMAT_OPTIONS[formatIndex].format);
  const isActiveCurrencyRequired = checkIsActiveCurrencyRequired(
    FORMAT_OPTIONS[formatIndex].format,
  );
  const isTimezoneDiffRequired = checkIsTimezoneDiffRequired(FORMAT_OPTIONS[formatIndex].format);

  function onAction(actionIndex: number) {
    // client/server interface
    const fetchParams = {
      userId: USER_ID,
      spreadsheetId,
      ...(isNumeric(sheetId) && { sheetId }),
      ...(isNumeric(sheetIdExtra) && { sheetIdExtra }),
      ...(activeCurrency && { activeCurrency }),
      ...(dateRange && dateRange[0] && { syncFrom: dateRange[0] }),
      ...(dateRange && dateRange[1] && { syncUntil: dateRange[1] }),
      ...(isNumeric(timeDiff) && { timeDiff }),
      formatOption: getFormatOption(formatIndex),
      resolveOption: getResolveOption(actionIndex, resolveOptionSelections as [boolean, boolean]),
    };

    if (isDisabled || !validateInputs(fetchParams)) {
      return;
    }

    onFetchAction(ENDPOINTS.apiSync, fetchParams);
  }

  return (
    <>
      <Inputs.Text
        labelText="Sheet ID"
        placeholder="default: 0"
        value={`${sheetId || ''}`}
        onChangeText={
          (newText) => (newText !== '') && setSheetId(parseInt(newText))
        }
      />

      <Inputs.Text
        labelText="Sheet ID Extra"
        placeholder="required"
        isHidden={!isSheetIdExtraRequired}
        isRequired={isSheetIdExtraRequired}
        value={`${sheetIdExtra || ''}`}
        onChangeText={
          (newText) => (newText !== '') && setSheetIdExtra(parseInt(newText))
        }
      />

      <Inputs.Text
        labelText="Active Currency"
        isHidden={!isActiveCurrencyRequired}
        isRequired={isActiveCurrencyRequired}
        value={activeCurrency || 'EUR'}
        onChangeText={(newText) => setActiveCurrency(newText)}
      />

      <Inputs.TextRange
        labelText="Date Range"
        placeholders={[
          'default: previous sync',
          'default: 100 records',
        ]}
        extraText="(yyyy-mm-dd)"
        type="date"
        values={dateRange}
        onChange={setDateRange}
      />

      <Inputs.SelectButtons
        labelText="Format"
        isRequired
        style={{ zIndex: 1 }}
        options={FORMAT_OPTIONS.map((option) => option.label)}
        selectedIndex={formatIndex}
        extraText={FORMAT_OPTIONS[formatIndex].extraText}
        onChange={setFormatIndex}
      />
      <Inputs.Text
        labelText=""
        isHidden={!isTimezoneDiffRequired}
        isRequired={isTimezoneDiffRequired}
        placeholder="timezone diff"
        extraText="(timezone-diff from UK - e.g. 0, 2, 3, -5)"
        value={`${timeDiff || ''}`}
        onChangeText={
          (newText) => (newText !== '') && setTimeDiff(parseInt(newText))
        }
      />

      <Inputs.SelectCheckboxes
        labelText="Resolve Previous"
        options={[
          'confirm',
          'revert',
        ]}
        selectedIndexes={resolveOptionSelections}
        shouldAllowMultiple={false}
        onChange={setResolveOptionSelections}
      />

      <Main.Actions
        actions={[
          'Sync Transactions',
          'Just Confirm',
          'Just Revert',
        ]}
        isDisabled={isDisabled}
        onAction={onAction}
      />
    </>
  );
}

function validateInputs({
  userId,
  spreadsheetId,
  sheetId,
  sheetIdExtra,
  activeCurrency,
  syncFrom,
  syncUntil,
  timeDiff,
  formatOption,
  resolveOption,
}: Partial<SyncMonzoHandlerBodyParams>) {
  if (!userId) {
    alert('Missing [userId]'); // eslint-disable-line no-alert
    return false;
  }

  if (!spreadsheetId) {
    alert('Missing [spreadsheetId]'); // eslint-disable-line no-alert
    return false;
  }

  if (!isNumeric(sheetId) && formatOption === 'holiday') {
    alert('[sheetId] is required in holiday mode'); // eslint-disable-line no-alert
    return false;
  }

  if (!isNumeric(sheetIdExtra) && checkIsSheetIdExtraRequired(formatOption)) {
    alert(`Missing [sheetIdExtra] for format ${formatOption}`); // eslint-disable-line no-alert
    return false;
  }

  if (!activeCurrency && checkIsActiveCurrencyRequired(formatOption)) {
    alert(`Missing [activeCurrency] for format ${formatOption}`); // eslint-disable-line no-alert
    return false;
  }

  const datePattern = /[0-9]{4}-[0-9]{2}-[0-9]{2}/;
  if (syncFrom && !datePattern.test(syncFrom)) {
    alert('Invalid [syncFrom] date format'); // eslint-disable-line no-alert
    return false;
  }
  if (syncUntil && !datePattern.test(syncUntil)) {
    alert('Invalid [syncUntil] date format'); // eslint-disable-line no-alert
    return false;
  }

  if (!isNumeric(timeDiff)) {
    if (checkIsTimezoneDiffRequired(formatOption)) {
      alert(`Missing [timeDiff] for format ${formatOption}`); // eslint-disable-line no-alert
      return false;
    }
  }

  // if (!formatOption) {} // not required

  if (!resolveOption) {
    alert('Missing [resolveOption]'); // eslint-disable-line no-alert
    return false;
  }

  return true;
}

const FORMAT_OPTIONS: FormatOption[] = [
  { format: 'remote', label: 'local & gbp', extraText: '(single sheets, formatted)' },
  { format: 'holiday', label: 'holiday mode', extraText: '(two sheets, not formatted)' },
  { format: 'local', label: 'local only', extraText: '(single sheet, formatted)' },
  { format: '', label: 'gbp only', extraText: '(single sheet, formatted)' },
  { format: 'none', label: 'no format', extraText: '(single sheet, not formatted)' },
];

function getFormatIndex(formatOption?: string) {
  return Math.max(
    FORMAT_OPTIONS.findIndex((option) => option.format === formatOption) || 0,
    0,
  );
}

function checkIsSheetIdExtraRequired(formatOption?: TransactionFormatOption) {
  return formatOption === 'remote' || formatOption === 'holiday';
}

function checkIsActiveCurrencyRequired(formatOption?: TransactionFormatOption) {
  return checkIsSheetIdExtraRequired(formatOption) || formatOption === 'local';
}

function checkIsTimezoneDiffRequired(formatOption?: TransactionFormatOption) {
  return checkIsActiveCurrencyRequired(formatOption);
}

function getFormatOption(selectedFormatIndex: number) {
  return FORMAT_OPTIONS[selectedFormatIndex].format;
}

function getResolveOption(
  actionIndex: number,
  selectedResolveOptions: [boolean, boolean],
): TransactionResolveOption {
  if (actionIndex === 1) {
    return 'confirmOnly';
  }

  if (actionIndex === 2) {
    return 'revertOnly';
  }

  if (selectedResolveOptions[0]) {
    return 'confirm';
  }

  if (selectedResolveOptions[1]) {
    return 'revert';
  }

  throw new Error(`Invalid getResolveOption inputs '${actionIndex}', '${selectedResolveOptions}'`);
}

type SyncTransactionsProps = {
  spreadsheetId?: string,
  cachedFields?: Partial<SyncMonzoHandlerBodyParams>,
  isDisabled?: boolean,
  onFetchAction: OnFetchFn,
};

type FormatOption = {
  format: TransactionFormatOption,
  label: string,
  extraText: string,
};
