import React, { useState } from 'react';
import { ENDPOINTS, USER_ID, isNumeric } from 'src/globs';
import { Inputs, Main } from 'src/Components';
import {
  OnFetchFn, TransactionResolveOption,
  ProvisionExpensesHandlerBodyParams, ResolveExpensesHandlerBodyParams,
} from 'src/types';

export function SetExpenses({
  spreadsheetId,
  cachedFields,
  isDisabled,
  onFetchAction,
}: SyncExpensesProps) {
  // todo temp page (maybe move to redux?)
  const [sourceSheetId, setSourceSheetId] = useState(cachedFields?.sourceSheetId);
  const [rowRange, setRowRange] = useState(['', ''] as [string, string]);
  const [exchangeRate, setExchangeRate] = useState(`${cachedFields?.exchangeRate || ''}`);

  function onAction(actionIndex: number) {
    // client/server interface
    const apiEndpoint = [
      ENDPOINTS.apiProvision,
      ENDPOINTS.apiResolve,
      ENDPOINTS.apiResolve,
    ][actionIndex];
    const resolveOption = ['', 'confirmOnly', 'revertOnly'][actionIndex] as TransactionResolveOption;

    const fetchParams = {
      userId: USER_ID,
      spreadsheetId,
      ...(isNumeric(sourceSheetId) && { sourceSheetId }),
      ...(rowRange && (rowRange[0] || rowRange[1]) && { range: `${rowRange[0]}:${rowRange[1]}` }),
      ...(exchangeRate && { exchangeRate: parseFloat(exchangeRate) }),
      ...(resolveOption && { resolveOption }),
    };

    if (isDisabled || !validateInputs(fetchParams)) {
      return;
    }

    onFetchAction(
      apiEndpoint,
      fetchParams,
    );
  }

  return (
    <>
      <Inputs.Text
        labelText="Source Sheet ID"
        placeholder="default: 0"
        value={`${sourceSheetId || ''}`}
        onChangeText={(newText) => setSourceSheetId(parseInt(newText))}
      />

      <Inputs.TextRange
        labelText="Row Range"
        placeholders={[
          'required for provision',
          'required for provision',
        ]}
        extraText="e.g. 30:70"
        values={rowRange}
        onChange={setRowRange}
      />

      <Inputs.Text
        labelText="Exchange Rate"
        placeholder="default: 1"
        value={`${exchangeRate || ''}`}
        onChangeText={setExchangeRate}
      />

      <Main.Actions
        actions={[
          'Provision Expenses',
          'Just Confirm',
          'Just Revert',
        ]}
        isDisabled={isDisabled}
        onAction={onAction}
      />
    </>
  );
}

function validateInputs({
  userId,
  spreadsheetId,
  sourceSheetId,
  range,
  exchangeRate,
  resolveOption,
}: Partial<ProvisionExpensesHandlerBodyParams & ResolveExpensesHandlerBodyParams>) {
  if (!userId) {
    alert('Missing [userId]'); // eslint-disable-line no-alert
    return false;
  }

  if (!spreadsheetId) {
    alert('Missing [spreadsheetId]'); // eslint-disable-line no-alert
    return false;
  }

  if (sourceSheetId && !isNumeric(sourceSheetId)) {
    alert('Non-numeric [sourceSheetId]'); // eslint-disable-line no-alert
    return false;
  }

  if (!range) {
    // required in provision step
    if (!resolveOption) {
      alert('Missing [range] for provision-expenses'); // eslint-disable-line no-alert
      return false;
    }
  } else {
    // if one range exists, both required
    const rangePattern = /[0-9]+:[0-9]+/;
    if (!rangePattern.test(range)) {
      alert('invalid row range'); // eslint-disable-line no-alert
      return false;
    }
  }

  if (exchangeRate !== undefined && (!isNumeric(exchangeRate) || exchangeRate < 1)) {
    alert(`Invalid [exchangeRate] (${exchangeRate})`); // eslint-disable-line no-alert
    return false;
  }

  // resolveOption

  return true;
}

interface SyncExpensesProps {
  spreadsheetId?: string,
  cachedFields?: Partial<ProvisionExpensesHandlerBodyParams & ResolveExpensesHandlerBodyParams>,
  isDisabled?: boolean,
  onFetchAction: OnFetchFn,
}
