import React from 'react';
import {
  StyleSheet,
  Text,
  TextInput,
  View,
} from 'react-native';
import { CommonProps } from 'src/types';
import { RowWrap } from './RowWrap';

export function TextRange({
  style,
  labelText,
  isRequired,
  isHidden,
  placeholders,
  extraText,
  values,
  onChange,
}: TextRangeProps) {
  return (
    <RowWrap
      style={style}
      labelText={labelText}
      isRequired={isRequired}
      isHidden={isHidden}
    >
      <View style={styles.inputsWrapper}>
        <TextInput
          style={styles.input}
          placeholder={(placeholders && placeholders.length && placeholders[0]) || undefined}
          value={values.length && values[0]}
          placeholderTextColor={styles.input.placeHolderColor}
          onChangeText={(newText) => onChange && onChange([newText, values[1]])}
        />
        <Text style={styles.dash}>-</Text>
        <TextInput
          style={styles.input}
          placeholder={(placeholders && placeholders.length > 1 && placeholders[1]) || undefined}
          value={values.length && values[1]}
          placeholderTextColor={styles.input.placeHolderColor}
          onChangeText={(newText) => onChange && onChange([values[0], newText])}
        />

        <Text style={styles.extraText}>{extraText}</Text>
      </View>
    </RowWrap>
  );
}

interface TextRangeProps extends CommonProps {
  labelText: string,
  isRequired?: boolean,
  isHidden?: boolean,
  placeholders?: [string, string],
  extraText?: string,
  type?: string,
  values: [string, string],
  onChange: (values: [string, string]) => void,
}

const styles = StyleSheet.create({
  inputsWrapper: {
    display: 'flex',
    flexDirection: 'row',
    flexWrap: 'wrap',
    flex: 1,
  },
  input: {
    padding: 10,
    paddingTop: 6,
    paddingBottom: 6,
    borderWidth: 1,
    borderColor: '#eee',
    fontSize: 16,
    placeHolderColor: '#aaa',
  },
  dash: {
    padding: 6,
    fontSize: 16,
    color: '#999',
  },
  extraText: {
    paddingTop: 6,
    paddingLeft: 12,
    fontSize: 16,
    color: '#ccc',
  },
});
