import React from 'react';
import {
  StyleSheet,
  Text,
  // View,
  ActivityIndicator,
  TouchableOpacity,
} from 'react-native';
import { CommonProps } from 'src/types';

export function Confirmation({
  isLoading,
  hasError,
  onPress,
  children,
}: ConfirmationProps) {
  // views don't have onPress event (need onClick)!
  return (
    <TouchableOpacity
      style={[
        styles.wrapper,
        isLoading ? styles.wrapperLoading : {},
        (!isLoading && hasError) ? styles.wrapperError : {},
        (!isLoading && !hasError) ? styles.wrapperSuccess : {},
        isLoading || children ? styles.wrapperVisible : {},
      ]}
      onPress={onPress}
    >
      { isLoading && <ActivityIndicator size="small" color="mediumseagreen" /> }
      {
        !isLoading && (typeof children === 'string'
          ? <Text style={styles.text}>{children}</Text>
          : children)
      }
    </TouchableOpacity>
  );
}

interface ConfirmationProps extends CommonProps {
  isLoading?: boolean,
  hasError?: boolean,
}

const styles = StyleSheet.create({
  wrapper: {
    height: 0,
    flexDirection: 'row',
    justifyContent: 'center',
    cursor: 'pointer',
    transition: 'transform 0.5s ease, height 0.5s ease',
    // @ts-ignore
    transform: 'translate(0, -100%)',
  },
  wrapperLoading: {
    backgroundColor: '#f7f7f7',
  },
  wrapperSuccess: {
    backgroundColor: 'mediumseagreen',
    // @ts-ignore
    transition: 'background-color 0.5s ease',
  },
  wrapperError: {
    backgroundColor: 'moccasin',
    // @ts-ignore
    transition: 'background-color 0.5s ease',
  },
  wrapperVisible: {
    padding: 10,
    minHeight: 40,
    height: 'default',
    display: 'flex',
    // @ts-ignore
    transform: 'translate(0, 0)',
  },
  text: {
    color: 'black',
  },
});
