import React, { useState } from 'react';
import {
  StyleSheet, Text, TextStyle,
} from 'react-native';
import { CommonProps } from 'src/types';

export function TextLink({
  style,
  hoverStyle,
  isDisabled,
  href,
  hrefAttrs,
  children,
}: TextLinkParams) {
  const [hasHover, setHasHover] = useState(false);

  return (
    <Text
      style={[
        styles.textLink,
        hasHover && styles.linkHasHover,
        style,
        hasHover && hoverStyle,
        isDisabled && styles.linkIsDisabled,
      ]}
      // @ts-ignore
      onMouseEnter={() => setHasHover(true)}
      onMouseLeave={() => setHasHover(false)}
      href={href}
      hrefAttrs={href && { ...DEFAULT_HREF_ATTRS, ...hrefAttrs }}
    >
      {children}
    </Text>
  );
}

interface TextLinkParams extends CommonProps {
  hoverStyle?: TextStyle,
  isDisabled?: boolean,
  href: string,
  hrefAttrs?: Record<string, string>,
}

const DEFAULT_HREF_ATTRS = {
  target: '_blank',
};

const styles = StyleSheet.create({
  textLink: {
    padding: 5,
    textAlign: 'center',
    justifyContent: 'center',
    // @ts-ignore
    cursor: 'pointer',
    color: '#666',
    transition: 'opacity 0.5s ease, background-color 0.5s ease, border-color 0.5s ease',
  },
  linkHasHover: {
    textDecorationLine: 'underline',
    textDecorationStyle: 'solid',
  },
  linkIsDisabled: {
    opacity: 0.5,
  },
});

TextLink.styles = styles;
