import React from 'react';
import {
  StyleSheet,
  TextInput as RNTextInput,
  Text as RNText,
} from 'react-native';
import { CommonProps } from 'src/types';
import { RowWrap } from './RowWrap';

export function Text({
  style,
  labelText,
  isRequired,
  isHidden,
  placeholder,
  value,
  extraText,
  onChangeText,
}: TextProps) {
  return (
    <RowWrap
      style={style}
      labelText={labelText}
      isRequired={isRequired}
      isHidden={isHidden}
    >
      <RNTextInput
        style={styles.input}
        placeholder={placeholder}
        value={value}
        placeholderTextColor={styles.input.placeHolderColor}
        onChangeText={onChangeText}
      />

      <RNText style={styles.extraText}>{extraText}</RNText>
    </RowWrap>
  );
}

interface TextProps extends CommonProps {
  labelText: string,
  isRequired?: boolean,
  isHidden?: boolean,
  placeholder?: string,
  value?: string,
  extraText?: string,
}

const styles = StyleSheet.create({
  input: {
    padding: 10,
    paddingTop: 6,
    paddingBottom: 6,
    borderWidth: 1,
    borderColor: '#eee',
    fontSize: 16,
    placeHolderColor: '#aaa',
  },
  extraText: {
    paddingTop: 6,
    paddingLeft: 12,
    fontSize: 16,
    color: '#ccc',
  },
});
