import React from 'react';
import { StyleSheet, View } from 'react-native';
import { Inputs } from 'src/Components/Inputs';
import { CommonProps } from 'src/types';

export function Actions({ actions, isDisabled, onAction }: ActionsProps) {
  return (
    <View style={styles.wrapper}>
      <Inputs.TextButton
        style={[styles.button, styles.actionPrimary]}
        isDisabled={isDisabled}
        onPress={() => onAction(0)}
      >
        {actions[0]}
      </Inputs.TextButton>

      <View style={styles.wrapperRight}>
        {
          actions.slice(1).map((actionText, index) => (
            <Inputs.TextButton
              style={[styles.button, styles.actionOthers]}
              hoverStyle={{ backgroundColor: '#f7f7f7' }}
              isDisabled={isDisabled}
              onPress={() => onAction(index + 1)}
              key={`action-${index}`}
            >
              {actionText}
            </Inputs.TextButton>
          ))
        }
      </View>
    </View>
  );
}

interface ActionsProps extends CommonProps {
  actions: string[],
  isDisabled?: boolean,
  onAction: (index: number) => void,
}

const styles = StyleSheet.create({
  wrapper: {
    marginTop: 30,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
  },
  wrapperRight: {
    display: 'flex',
    flexDirection: 'row',
  },
  button: {
    padding: 15,
    fontSize: 20,
    borderRadius: 5,
    cursor: 'pointer',
  },
  actionPrimary: {
    backgroundColor: 'dodgerblue',
    border: 'none',
    color: 'white',
  },
  actionOthers: {
    marginLeft: 10,
    backgroundColor: 'white',
    borderColor: '#ccc',
    color: '#999',
  },
});
