import React from 'react';
import {
  // @ts-ignore
  StyleSheet, Text, CheckBox, View,
} from 'react-native';
import { CommonProps } from 'src/types';
import { RowWrap } from './RowWrap';

export function SelectCheckboxes({
  style,
  labelText,
  isRequired,
  isHidden,
  options,
  selectedIndexes,
  shouldAllowMultiple,
  onChange,
}: SelectCheckboxesProps) {
  return (
    <RowWrap
      style={style}
      labelText={labelText}
      isRequired={isRequired}
      isHidden={isHidden}
    >
      {
        options.map((optionText, index) => (
          <View style={styles.group} key={`box-${index}`}>
            <CheckBox
              style={styles.checkbox}
              value={selectedIndexes.length === options.length && selectedIndexes[index]}
              onValueChange={(newValue: boolean) => {
                const newSelections = shouldAllowMultiple
                  ? [...selectedIndexes]
                  : selectedIndexes.map(() => false);
                newSelections[index] = newValue;
                onChange(newSelections);
              }}
            />
            <Text style={styles.text}>{optionText}</Text>
          </View>
        ))
      }
    </RowWrap>
  );
}

interface SelectCheckboxesProps extends CommonProps {
  labelText: string,
  isRequired?: boolean,
  isHidden?: boolean,
  options: string[],
  selectedIndexes: boolean[],
  shouldAllowMultiple?: boolean,
  onChange: (values: boolean[]) => void,
}

const styles = StyleSheet.create({
  group: {
    display: 'flex',
    flexDirection: 'row',
    marginRight: 12,
  },
  checkbox: {
    marginRight: 8,
    width: 24,
    height: 24,
  },
  text: {
    paddingTop: 2,
    fontSize: 16,
  },
});
