import { useState } from 'react';

export function useFetchWithStates<T1, T2>(): UseFetchWithStatesHook<T1, T2> {
  const [responseJson, setResponseJson] = useState<T2>();
  const [isLoading, setIsLoading] = useState(false);
  const [hasError, setHasError] = useState(false);

  async function doFetch(endpoint: string, params: T1) {
    setIsLoading(true);
    const response = await fetch(endpoint, {
      method: 'POST',
      body: JSON.stringify(params),
    });

    const data = await response.json() as T2;

    setResponseJson(data);
    setIsLoading(false);
    setHasError(!response.ok);
  }

  function doReset() {
    setResponseJson(undefined);
    setIsLoading(false);
    setHasError(false);
  }

  return [responseJson, isLoading, hasError, doFetch, doReset];
}

type DoFetchFn<T1> = (
  endpoint: string,
  params: T1,
) => Promise<void>;

type DoResetFn = () => void;

type UseFetchWithStatesHook<T1, T2> = [
  T2 | undefined, boolean, boolean, DoFetchFn<T1>, DoResetFn,
];
