import React, { useState } from 'react';
import { StyleSheet, View } from 'react-native';
import { useFetchWithStates, useLocalstorage } from 'src/utils';
import {
  Header, Main, Inputs, Footer,
} from 'src/Components';
import {
  LocalCache, FetchApiParams, FetchApiResponse, OnFetchFn,
} from 'src/types';
import * as Pages from '../Pages';

export function App() {
  // // todo temp (maybe move to redux?)
  const [cachedFields, setCachedFields] = useLocalstorage<LocalCache>(
    'cachedFields',
    {} as LocalCache,
  );
  const [spreadsheetId, setSpreadsheetId] = useState(cachedFields.spreadsheetId);
  const [selectedTabIndex, setTabIndex] = useState(0);

  const [
    fetchResult,
    isFetchLoading,
    hasFetchError,
    doFetch,
    doReset,
  ] = useFetchWithStates<FetchApiParams, FetchApiResponse>();

  const onFetchAction: OnFetchFn = (endpoint: string, params: LocalCache) => {
    setCachedFields({
      ...cachedFields,
      ...params,
    });
    doFetch(endpoint, params as FetchApiParams);
  };

  const fetchResultMessage = getFetchResultMessage(fetchResult);

  return (
    <View style={styles.app}>
      <Header.Container>
        <Header.LeftRight>
          <Header.Left>
            <Header.Title>mDiary Client</Header.Title>
          </Header.Left>
          <Header.Right>
            <Inputs.Text
              labelText="Spreadsheet ID"
              style={{ marginTop: 0 }}
              placeholder="required"
              value={spreadsheetId}
              onChangeText={setSpreadsheetId}
              isRequired
            />
          </Header.Right>
        </Header.LeftRight>
        <Header.Tabs
          labels={['Sync Transactions', 'Set Expenses', 'Instructions']}
          selectedIndex={selectedTabIndex}
          onChangeTab={setTabIndex}
        />
      </Header.Container>
      <Main.Container>
        <Main.Confirmation
          isLoading={isFetchLoading}
          hasError={hasFetchError}
          onPress={doReset}
        >
          {fetchResultMessage}
        </Main.Confirmation>

        { selectedTabIndex === 0 && (
          <Pages.SyncTransactions
            spreadsheetId={spreadsheetId}
            cachedFields={cachedFields}
            isDisabled={isFetchLoading}
            onFetchAction={onFetchAction}
          />
        )}
        { selectedTabIndex === 1 && (
          <Pages.SetExpenses
            spreadsheetId={spreadsheetId}
            cachedFields={cachedFields}
            isDisabled={isFetchLoading}
            onFetchAction={onFetchAction}
          />
        )}
        { selectedTabIndex === 2 && (
          <Pages.Instructions />
        )}
      </Main.Container>
      <Footer.Container>
        <Footer.AuthLinks />
      </Footer.Container>
    </View>
  );
}

const styles = StyleSheet.create({
  app: {
    width: '100%',
    maxWidth: 960,
    backgroundColor: 'white',
  },
});

function getFetchResultMessage(fetchResult?: FetchApiResponse): string {
  if (fetchResult) {
    // set result message
    if (fetchResult?.syncedRowsCount) {
      return `${fetchResult?.syncedRowsCount} rows synced`;
    }

    if (fetchResult?.provisionedRowsCount) {
      return `${fetchResult?.provisionedRowsCount} rows provisioned`;
    }

    if (fetchResult?.expensedRowsCount) {
      return `${fetchResult?.confirmedRowsCount} rows expensed`;
    }

    if (fetchResult?.apiError) {
      return JSON.stringify(fetchResult?.apiError);
    }

    // unexpected
    return JSON.stringify(fetchResult);
  }

  return '';
}
